<template>
    <div>
        <!--Table Data Kategori-->
        <vx-card>
        <vs-table stripe search pagination  max-items="10" :data="kategori" >
            <template slot="header" class="mb-5">
                <h1 class="mr-3">Data Kategori</h1>
                <!-- <vs-button @click="btnAddKategori" icon="add" color="#0fabbc" type="filled">Tambah Data</vs-button> -->
            </template>
            
            <template slot="thead">
                <vs-th sort-key="kategori" class="bg-primary text-white">
                   Nama
                </vs-th>
            </template>

            <template slot-scope="{data}">
                <vs-tr :key="items.kode" v-for="items in data" >
                    <vs-td :data="items.kategori">
                        {{items.kategori}}
                    </vs-td>
                    <!-- <vs-td :data="items.aksi">
                    <vs-button @click="editData(items.kode, items.kategori)" class="mr-1" color="success" type="filled" size="small" icon="create" style="display:inline"></vs-button>
                    <vs-button @click="deleteData(items.kode)" color="danger" type="filled" size="small" icon="delete" style="display:inline"></vs-button>
                    </vs-td> -->
                </vs-tr>
            </template>
        </vs-table>
        </vx-card>

        <!-- Popup Add Kategori -->
        <vs-popup class="holamundo"  title="Add Kategori" :active.sync="addNew">
                <div class="vx-row mb-6">
                    <div class="vx-col w-full">
                    <vs-input class="w-full" label="Nama Kategori" v-model="form.kategori" />
                    </div>
                </div>
                <div class="vx-row">
                    <div class="vx-col w-full text-right">
                    <vs-button color="success" class="mr-3 mb-2" @click=" addData()">Submit</vs-button>
                    </div>
                </div>
        </vs-popup>

        <!-- Popup Edit KKategori -->
        <vs-popup class="holamundo"  title="Edit Kategori" :active.sync="Edit">
                <div class="vx-row mb-6">
                    <div class="vx-col w-full">
                    <vs-input class="w-full" label="Nama Kategori" v-model="form.kategori" />
                    </div>
                </div>
                <div class="vx-row">
                    <div class="vx-col w-full text-right">
                    <vs-button color="success" class="mr-3 mb-2" @click=" updateData(form.kode)">Submit</vs-button>
                    </div>
                </div>
        </vs-popup>
    </div>
</template>

<script>
export default {
    data() {
        return {
            publicPath: process.env.BASE_URL,
            addNew:false,
            Edit:false,
            kategori:[],
            form: {
                kode:'',
                kategori:'',
            }

        }
    },

    created() {
       this.loadData();
    },

    methods:{
        btnAddKategori(){
            this.addNew = true,
            this.form.kategori = ''
        },

        loadData(){
            this.$http.get('https://117.53.47.96:5560/agen/web/produk/kategori')
            .then(response => {
                this.kategori = response.data.message
            })
            .catch(err=>
                    this.$swal.fire(
                        'Gagal !',
                        err.response.data.message,
                        'error'
                        )
                    )
        },

        addData(){
            this.$vs.loading();
            const postData = {
                kategori: this.form.kategori,
            }

            this.$http.post('https://117.53.47.96:5560/agen/web/produk/kategori/create', postData)
            .then(response => {
  
                if(response.data.status == "SUKSES"){
                    this.form.kategori = '',
                    this.addNew = false

                    this.$swal.fire({
                    title: 'Success',
                    text: response.data.message,
                    icon: 'success',

                    }).then(() => {
                        this.loadData();
                    })
                }else {
                    this.$swal.fire(
                        'Gagal !',
                        response.data.message,
                        'error'
                    )
                }
            })
            .catch(err=>{
                    this.$vs.loading.close()
                    this.$swal.fire(
                        'Gagal !',
                        err.response.data.message,
                        'error'
                        )
                    })
            .finally(() => {this.$vs.loading.close()})
        },

        editData(kode, kategori){
            this.Edit = true
            this.form.kode = kode
            this.form.kategori = kategori
        },

        updateData(){
            this.$vs.loading();
            const updtData = {
                kode: this.form.kode,
                kategori: this.form.kategori,
            }
            this.$http.put('#', updtData)
            .then(response => {

                if(response.data.status == "SUKSES"){
                    this.form.kategori = '',
                    this.addNew = false

                    this.$swal.fire({
                    title: 'Success',
                    text: response.data.message,
                    icon: 'success',

                    }).then(() => {
                        this.loadData();
                    })
                }else {
                    this.$swal.fire(
                        'Gagal !',
                        response.data.message,
                        'error'
                    )
                }
            })
            .catch(err=>{
                    this.$vs.loading.close()
                    this.$swal.fire(
                        'Gagal !',
                        err.response.data.message,
                        'error'
                        )
                    })
            .finally(() => {this.$vs.loading.close()});
        }
    }

}
</script>